.auto-scroll-container {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.auto-scroll-text {
  white-space: nowrap;
  display: inline-block;
  min-width: 100%;
  will-change: transform;
}

/* Single continuous animation loop */
.auto-scroll-text.scrolling {
  animation: auto-scroll-cycle var(--total-duration, 22s) linear infinite;
}

@keyframes auto-scroll-cycle {
  /* Initial pause: 0% to ~13.6% (3s of 22s) */
  0%, 13.6% {
    transform: translateX(0);
  }
  /* Scroll forward: 13.6% to 50% (8s) */
  50% {
    transform: translateX(var(--scroll-distance, -50%));
  }
  /* Middle pause: 50% to 63.6% (3s) */
  63.6% {
    transform: translateX(var(--scroll-distance, -50%));
  }
  /* Scroll back: 63.6% to 100% (8s) */
  100% {
    transform: translateX(0);
  }
}